import React from 'react';
import { string, shape, bool } from 'react-proptypes';

import { SignupForm } from '../../../components/SignupForm';
import { TermsLink } from '../../../components/TermsLink';

import styles from './MainBlock.module.scss';

const MainBlock = ({ params, previewing }) => (
    <div className={styles.mainBlock__container}>

        <div className={styles.mainBlock__title} 
            style={{ backgroundImage: `url(${process.env.ASSETS_URL}${params.bgImage})` }} >
            <div className={styles.mainBlock__content} >
                <h1>
                    {params.title}
                </h1>
            </div>
        </div>

        <div className={styles.mainBlock__content}>
            <p  className={`copytext-large ${styles.mainBlock__description}`}
                dangerouslySetInnerHTML={{ __html: params.description }}>
            </p>

            <SignupForm
                buttonText={params.buttonText}
                disabled={ previewing }
                inputPlaceholder={params.inputPlaceholder}
            />

            <TermsLink />

        </div>
        
    </div>
);

MainBlock.propTypes = {
    params: shape({
        title: string,
        description: string,
        inputPlaceholder: string,
        buttonText: string
    }),
    previewing: bool
};

MainBlock.defaultProps = {
    params: {},
    previewing: false
};

export default MainBlock;



